.sort-btn {
  border: 1.3px solid $main-blue-color;
  color: $main-blue-color;
  border-radius: 5px;
  display: flex;
  font-size: 12px;
  align-items: center;
}
.product {
  // margin-bottom: 600px;
  &__breadcrumb {
    margin: 0 0 30px 0;
  }
  &__page-content {
    display: flex;
    margin: 0 0 100px 0;
  }
  /* Sort by Section */
  &__sortby-header {
    display: flex;
    justify-content: space-between;
    margin: 0 0 40px 0;
    .label {
      font-size: 25px;
      color: $main-black-color;
      text-transform: uppercase;
      font-weight: 700;
    }
  }
  .sortby-controls {
    display: flex;
    align-items: center;
    &__label {
      color: $main-black-color;
      font-size: 14px;
      font-weight: 600;
      margin-right: 20px;
    }
    &__price {
      padding: 10px 10px;
      margin-right: 20px;
    }
    &__params {
      padding: 10px 15px;
    }
    img {
     margin-left: 15px; 
    }
  }

  // Sidebar filter Section
  .page-sidebar {
    max-width: 255px;
    flex: 0 0 100%;
    background-color: $bg-color;
    margin: 0 40px 220px 0;
    height: 590px;
    &__container {
      max-width: 212px;
      margin: 0 auto;
      padding: 20px 0;
    }
  }
  .filter-block {
    margin: 0 0 20px 0;
    &__header {
      margin-bottom: 20px;
      font-weight: 600;
    }
    &__item {
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      color: $main-black-color;
      font-size: 14px;
      .filter {
        margin-right: 10px;
      }
    }
    &__item:last-child {
      margin: 0;
    }
    .filter-checkbox {
      position: relative;
      padding: 0 0 0 20px;
      line-height: 18px;
      
      input:checked + .filter-checkbox__hidden {
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        background-color: #fff;
        border: 0;
        border-radius: 2px;
        background-image: url('./images/filters/checked.svg');
        background-repeat: no-repeat;
        background-position: center;
      }
      &__input {
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
      }
      &__hidden {
        top: 0;
        left: 0;
        position: absolute;
        width: 16px;
        height: 16px;
        background-color: #fff;
        border: 1px solid #5D6C7B;
        border-radius: 2px;
      }
    }
  }
  .show-more-filters {
    font-size: 14px;
    color: $main-blue-color;
    text-align: center;
    img {
      padding: 0 5px;
    }
  }
  .price-range {
    margin: 0 0 17px 0;
  }
  .price-input {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__from {
      width: 68px;
      height: 35px;
      border: 1px solid #EAEBED;
      border-radius: 5px;
      padding-left: 10px;
      outline: none;
      border: 0;
    }
    &__to {
      width: 68px;
      height: 35px;
      border: 1px solid #EAEBED;
      border-radius: 5px;
      padding-left: 10px;
      outline: none;
      border: 0;
    }    
  }

  /* Sidebar adv section */

  .sidebar-adv {
    background: radial-gradient(102.16% 238.93% at -2.16% 100%, #FFCF71 0%, #F3A712 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    border-radius: 10px;
    margin-bottom: 20px;
    .product-of-month {
      &__container {
        display: flex;
        align-items: center;
      }
      &__header {
        height: 60px;
        border-bottom: 1px solid rgb(234, 234, 234);
        // border: 1px solid red;
        margin-top: -25px;
        margin-bottom: 15px;
      }
      &__icon {
        margin-right: 12px;
      }
      &__title {
        font-weight: 500;
        font-size: 18px;
        line-height: 26px;
        color: #FFFFFF;
      }
      &__image {
        background-color: #F0F1F5;
        width: 61px;
        height: 61px;
        border-radius: 5px;
        margin-right: 20px;
        img {
          margin: 10px 0 0 0;
        }
      }
      &__label {
        font-weight: 400;
        font-size: 16px;
        line-height: 23px;
        color: #FFFFFF;
        padding: 0;
        margin: 0;
      }
      &__price {
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;
      }
    }
  }

  .sidebar-question {
    background: radial-gradient(71.16% 199.4% at 82.43% 76.38%, #8FE865 0%, #49AC1B 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    border-radius: 10px;
    height: 90px;

    &__container {
      display: flex;
      align-items: center;
      max-width: 212px;
      margin: 0 auto;
    }
    &__image {
      margin-left: -15px;
    }
    &__body {
      font-weight: 500;
      font-size: 16px;
      line-height: 18px;
      color: #FFFFFF;      
    }
  }
  
  /* Product Body section */
  .products-body {
    max-width: 825px;
    flex: 0 0 825px;
  }
  .products-list {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 30px;
  }
  .product-list-item {
    border: 1.5px solid #EAEBED;
    border-radius: 10px;
    max-width: 252px;
    margin: 0 20px 0 0;
    flex: 0 0 100%;
  }
  .product-thumbnail {
    background-color: #F0F1F5;
    &__container {
      padding: 10px;
    }
    img {
      width: 200px;
    }
  }
  .adv-block {
    background-color: $bg-color;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h4 {
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: #282739;
      padding: 0;
      margin: 0;
    }
    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: #282739;
    }
    &__cta {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: #6F73EE;
      display: flex;
      align-items: center;
      img {
        padding: 0 5px;
      }
    }
    .adv-block-body {
      img {
        margin-top: -50px;
        margin-bottom: -25px;
        width: 207px;
      }
    }

    &__container {
      padding: 35px 30px;
    }


  }
  .product-marketing {
    display: flex;
    justify-content: space-between;

    &__label {
      font-size: 12px;
      padding: 6px 10px;
      border-radius: 5px;
      height: 15px;
      line-height: 16px;
    }
    &__label.hit {
      background-color: #EE685F;
      text-transform: uppercase;
      color: #fff;
    }
    &__label.new {
      background-color: #75D14A;
      color: #fff;
    }
    &__compare {
      background-color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50px;
      width: 35px;
      height: 35px;
    }
    &__compare img {
      width: 20px;
      height: 12px;
    
    }
  }
  .product-body {
    &__container {
      padding: 20px 25px;
    }
    .product-title {
      font-weight: 700;
      margin: 0 0 20px 0;
      // font-weight: 600;
      font-size: 18px;
      line-height: 26px;
      color: #282739;
    }
    .params-list {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
      align-items: center;
      line-height: 28px;
      &__item {
        color: #5D6C7B;
        font-size: 14px;
        display: flex;  
      }
      &__item img {
        margin-right: 5px;
      }
    }
    .product-shopping {
      display: flex;
      justify-content: space-between;
      margin: 0 0 20px 0;
      max-width: 206px;
      .product-section-price {
        &__discount {
          color: $second-color;
          font-size: 14px;
          text-decoration: line-through;
        }
        &__current {
          font-size: 20px;
          font-weight: 700;
          text-transform: uppercase;
          color: $main-black-color;
          padding: 3px 0 0 0;
        }
      }
      .shopping-cart {
        display: flex;
        .circled:last-child {
          margin-left: 10px;
        }
        .circled {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          border: 1.3px solid #EAEBED;
          margin: 0px auto;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
    .buy-oneclick {
      border: 0;
      background-color: $main-blue-color;
      color: #fff;
      border-radius: 5px;
      font-size: 12px;
      width: 100%;
      height: 37px;
    }
  }

    // Subscription section

    &__subscription {
      background-color: $main-blue-color;
      height: 100px;   
      // margin-bottom: 50px; 
    }
    .product-subscription {

      .subscription-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      
      &__column {
        padding: 25px 0;
      }
      

      .subscription-label {
        width: 425px;
        font-weight: 600;
        font-size: 18px;
        line-height: 26px;
        text-transform: uppercase;
        color: #FFFFFF;
      }
      .subscription-form__form {
        display: flex;
        justify-content: space-between;
        width: 570px;
      }
      .subscription-form {
        display: flex;
        justify-content: space-between;
        &__input {
          background: rgba(255, 255, 255, 0.2);
          border-radius: 5px;
          border: 0;
          width: 395px;
          height: 53px;
          color: #fff;
          font-size: 16px;
          outline: none;
          padding-left: 15px;
          &::placeholder {
            color: #fff;
          } 
        }
        &__submit {
          background-color: #fff;
          border: 0;
          outline: none;
          width: 140px;
          height: 53px;
          border-radius: 5px;
          text-align: center;
          color: #6F73EE;
          font-size: 16px;
          &:hover {
            cursor: pointer;
          }
        }
      }
    }
}

.footer {
  padding: 55px 0 0 0;
  background-color: #F4F7FB;
  .footer-content {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
    &__header {
      display: flex;
      justify-content: space-between;
    }
    &__column:first-child {
      display: flex;
      justify-content: space-between;
      width: 510px;
    }
    &__column:last-child {
      width: 510px;
    }
    &__for-buyer {
      display: flex;
    }
    .buyer-column-1 {
      margin: 0 45px 0 0;
    }
    .nav-row-1 {
      display: flex;
      .footer-nav {
        margin-right: 50px;
      }
    }
    .nav-row-2 {
      display: flex;
      justify-content: space-between;
    }
  }
  .w-header {
    min-height: 25px;
  }
  .footer-nav {
    &__row {
      margin-bottom: 20px;
    }
    &__title {
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;      
      color: #282739;
      margin-bottom: 25px;
    }
    &__cta {
      font-weight: 500;
      font-size: 12px;
      line-height: 17px;
      color: #6F73EE;
    }
    &__list {
      padding: 0;
      list-style: none;
      li {
        margin: 0 0 10px 0;
      }
    }
    &__link {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #5D6C7B;
    }
    &__contact-phone_phone {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
      color: #282739;
    }
    &__contact {
      &.label {
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #5D6C7B;
      }
      &.number {
        font-weight: 500;
        font-size: 16px;
        line-height: 23px;        
        color: #282739;
      }
      &.time {
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #5D6C7B;
      }
    }
  }
  .footer-contact > div {
    margin-bottom: 5px;
  }

  .footer-social {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__column {
      display: flex;
      align-items: center;
    }
    &__logo {
      margin-right: 50px;
    }
    &__app-markets {
      display: flex;
      align-items: center;
      img:first-child {
        margin-right: 10px;
      }
    }
  }
  .f-social-block {
    display: flex;
    &__label {
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: #282739;
    }
    &__info {
      font-weight: 400;
      font-size: 10px;
      line-height: 14px;
      color: #5D6C7B;
    }
    &__item {
      display: flex;
      background-color: #fff;
      padding: 7px 12px 7px 12px;
      border-radius: 5px;
      margin-right: 10px;
      img {
        margin-right: 10px;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }

  
  .footer-copyright {
    display: flex;
    justify-content: space-between;

    &__container {
      display: flex;
      justify-content: space-between;
    }
    .footer-copyright-block {
      width: 520px;
    }
    &__list {
      display: flex;
      list-style: none;
      margin: 0;
      padding: 0;
      li:first-child {
        margin-right: 45px;
      }
    }
    &__link {
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #5D6C7B;
    }
    .payment-block {
      display: flex;
      
      &__container {
        background-color: #fff;
        border-radius: 3px;
        border: 1px solid #EAEBED;
        padding: 1px 5px;
        margin-right: 5px;
      }
      img {
        border-radius: 3px;  
        width: 24px;
        height: 10px;      
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .chat-block {
      width: 157px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  .bottom-h-line {
    border-bottom: 1.4px solid #dcdcdc;
    width: 1110px;
    margin: 30px 0;
  }
}